%dropdown {
  position: relative;
  font-size: 1rem;
  line-height: 1;
  font-style: normal;
  display: inline-block;
}

@mixin dropdown(
    $border: $global-menu-border-color,
    $color: $global-menu-color,
    $color-hover: $global-menu-color-hover,
    $background: $global-menu-background,
    $background-hover: $global-menu-background-hover,
    $radius: $global-menu-border-radius,
    $shadow: true,
    $divider: $global-menu-divider,
    $indent: $global-menu-indent,
    $menu: ".dropdown-menu",
    $trigger: ".dropdown-trigger"
  ) {

  @extend %dropdown;

  & > #{$menu} {
    @include menu(
      $border,
      $color,
      $color-hover,
      $background,
      $background-hover,
      $radius,
      $shadow,
      $divider,
      $indent
    );
  }

  // Open state for the dropdown
  &.open,
  &[open=true] {
    & > #{$menu} { display: block; }
    & > a { outline: 0; }
  }

  & > #{$trigger} {
    margin-bottom: 0;
  }
}

// Dropdown

.standard-dropdown {
  @include dropdown(
    $border: #8E24AA,
    $background: #BA68C8,
    $background-hover: #CE93D8,
    $color: #F3E5F5,
    $color-hover: #4A148C,
    $divider: #8E24AA,
    $menu: "ul",
    $trigger: ".standard-button"
  );
}
